import { Wilaya } from '../enums/wilaya.enum'
import { Niveau } from '../enums/niveau.enum'

export class ServiceContractant {
    public _id: string
    public identifiant: string
    public nomStructure: string
    public email: string
    public telephone: string[]
    public fax: string[]
    public adresse: string
    public wilaya: Wilaya
    public codePostal: string
    public niveau: Niveau


    constructor(
        identifiant: string,
        nomStructure: string,
        email: string,
        telephone: string[],
        fax: string[],
        adresse: string,
        wilaya: Wilaya,
        codePostal: string,
        niveau: Niveau
    ){
        this.identifiant=identifiant
        this.nomStructure=nomStructure
        this.email=email
        this.telephone=telephone
        this.fax=fax
        this.adresse=adresse
        this.wilaya=wilaya
        this.codePostal=codePostal
        this.niveau = niveau
    }

    static getDummy(): ServiceContractant {
        return new ServiceContractant(
            "DFC",
            "Direction Finances et comptabilite (DFC)",
            "email@test.com",
            ["023232323","024242424"],
            ["023232323","024242424"],
            "Rue 344 Alger Centre",
            Wilaya.Alger,
            "16000",
            Niveau.CENTRAL
        )
    }
    
  }