import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { CreateServiceContractantComponent } from '../dialogs/create-service-contractant/create-service-contractant.component';
import { ServiceContractant } from '../models/service.contractant.model';
import { ServiceContractantService } from '../domaine/business-logic/services-contractants.service';
import { DialogAction } from '../enums/dialog-action.enum';

@Component({
  selector: 'app-services-contractants',
  templateUrl: './services-contractants.component.html',
  styleUrls: ['./services-contractants.component.css'],
  //providers: [ServiceContractantService, ServiceContractantAccess]
})
export class ServicesContractantsComponent implements OnInit {

  servicesContractants: ServiceContractant[];
  loadingData = false;

  constructor(public dialog: MatDialog, public serviceContractantService: ServiceContractantService) {
    this.servicesContractants = [];
  }

  ngOnInit() {
    this.loadingData = true;
    const result:Promise<ServiceContractant[]> = this.serviceContractantService.getAllServicesContractants();
    result
    .then((value) =>{
      this.loadingData = false;
      return this.servicesContractants = value;
    })
    .catch(()=>{
      this.loadingData = false ;
      return this.servicesContractants = null;
    })
  }
  
  openDialogCreateServiceContractant(): void {

    const dialogRef = this.dialog.open(CreateServiceContractantComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CREATE, sc: null}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);
      if(this.servicesContractants && result) {
        this.servicesContractants.push(result);
      }
    });

    
  }

  openDialogUpdateServiceContractant(serviceContractant: ServiceContractant): void {
    
    console.log("serviceContractant", serviceContractant);

    const dialogRef = this.dialog.open(CreateServiceContractantComponent, 
      {
      width: '800px',
      data: {action: DialogAction.MODIFY, sc: serviceContractant}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      
      if(this.servicesContractants && result) {
        let sc = this.servicesContractants.find(e => e._id === result._id)
        //const index = this.servicesContractants.indexOf(sc);
        //this.servicesContractants.splice(index,1)
        for(var k in sc) sc[k]=result[k];
      }
      
    });

    
  }

  openDialogDeleteServiceContractant(serviceContractant: ServiceContractant): void {
    
    console.log("serviceContractant", serviceContractant)

    const dialogRef = this.dialog.open(CreateServiceContractantComponent, 
      {
      width: '800px',
      data: {action: DialogAction.DELETE, sc: serviceContractant}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      
      if(this.servicesContractants && result) {
        let sc = this.servicesContractants.find(e => e._id === result._id)
        const index = this.servicesContractants.indexOf(sc);
        this.servicesContractants.splice(index,1)
      }
      
    });

    
  }

}
