import { Component, ViewChild } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { Candidat } from 'src/app/models/candidat.model';
import { DemandeEclaircissement } from 'src/app/models/demande.eclaircissement.model';
import { CreateCandidatComponent } from 'src/app/dialogs/create-candidat/create-candidat.component';
import { CreateReponseDemandeEclaircissementComponent } from 'src/app/dialogs/create-response-demande-eclaircissement/create-response-demande-eclaircissement.component';
import { CreateDemandeEclaircissementComponent } from 'src/app/dialogs/create-demande-eclaircissement/create-demande-eclaircissement.component';

@Component({
    selector: 'app-retrait-cdc',
    templateUrl: './retrait_cdc.component.html',
    styleUrls: ['./retrait_cdc.component.css']
})

export class RetraitCDCComponent {
    constructor(public dialog: MatDialog) { }


    ngOnInit() {

    }


    @ViewChild("tableCandidats", {static: true}) tableCandidats: MatTable<Candidat>;
    @ViewChild("tableDemandesEclaircissement", {static: true}) tableDemandesEclaircissement: MatTable<DemandeEclaircissement>;


    dataSourceCandidats: Candidat[] = []
    displayedColumnsCandidats: string[] = [
        'position',
        'denominationEntreprise',
        'dateRetrait',
        'heureRetrait'
    ];


    
    dataSourceDemandesEclaircissement: DemandeEclaircissement[] = []
    displayedColumnsDemandesEclaircissement: string[] = [
        'identifiant',
        'dateDemande',
        'candidat',
        'demandePath',
        'dateNotificationSC',
        'ReponseEclaircissement'
    ];









    openDialogCreateCandidat() {
        const dialogRef = this.dialog.open(CreateCandidatComponent, {
            width: '800px',
            data: "nouveauLitige"
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            console.log(result);
        });

        this.dataSourceCandidats.push(Candidat.getDummy())
        this.tableCandidats.renderRows();
    }

    openDialogCreateDemandeEclaircissement() {
        const dialogRef = this.dialog.open(CreateDemandeEclaircissementComponent, {
            width: '800px',
            data: "nouvelleDem.Eclai"
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            console.log(result);
        });

        this.dataSourceDemandesEclaircissement.push(DemandeEclaircissement.getDummy())
        this.tableDemandesEclaircissement.renderRows();
    }

    openDialogCreateReponseDemandeEclaircissement() {
        const dialogRef = this.dialog.open(CreateReponseDemandeEclaircissementComponent, {
            width: '800px',
            data: "nouvelleDem.Eclai"
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            console.log(result);
        });

    }

}







