import { ReponseEclaircissement } from './reponse.eclaircissement.model'

export class DemandeEclaircissement {
    identifiant: string
    dateDemande: Date
    candidat: string 
    demandePath: string 
    dateNotificationSC: Date
    reponse: ReponseEclaircissement

    constructor(
        identifiant: string,
        dateDemande: Date,
        candidat: string ,
        demandePath: string ,
        dateNotificationSC: Date,
        reponse: ReponseEclaircissement
    ){
        this.identifiant= identifiant,
        this.dateDemande= dateDemande,
        this.candidat=candidat,
        this.demandePath=demandePath,
        this.dateNotificationSC = dateNotificationSC,
        this.reponse=reponse
    }
    static getDummy(){
        return new DemandeEclaircissement(
            "identifiant",
            new Date(),
            "candidat",
            'demandePath',
            new Date(),
            ReponseEclaircissement.getDummy()
        );
    }
}