export class OperateurEconomique {
    denomination: string;
    moyenNotification: string
    dateParution: Date;
    justificatif: string;

    constructor (
        denomination: string,
        moyenNotification: string,
        dateParution: Date,
        justificatif: string 
    ){
        this.denomination=denomination
        this.moyenNotification = moyenNotification
        this.dateParution = dateParution
        this.justificatif = justificatif
    }

    static getDummy():OperateurEconomique {
        return new OperateurEconomique(
            "Nom Op. Eco.",
            "Email",
            new Date(),
            "justificatifPath"
        )
    }
}