export class Candidat {
    id:string
    denominationEntreprise: string
    nom: string
    prenom:string
    qualite:string
    email:string 
    portable:string[]
    telephone:string[]
    fax:string[]
    dateEtHeureRetrait:Date

    constructor (
        denominationEntreprise: string,
        nom: string,
        prenom:string,
        qualite:string,
        email:string ,
        portable:string[],
        telephone:string[],
        fax:string[],
        dateEtHeureRetrait:Date
    ){
        this.denominationEntreprise=denominationEntreprise
        this.nom=nom
        this.prenom=prenom
        this.qualite=qualite
        this.email = email 
        this.portable = portable
        this.telephone = telephone,
        this.fax=fax,
        this.dateEtHeureRetrait = dateEtHeureRetrait
    }
    static getDummy(): Candidat {
        return new Candidat(
          "denominationEntreprise",
          "nom",
          "prenom",
          "qualite",
          "email@test.dz",
          ["0555999000","0566666666"],
          ["0555999000","0566666666"],
          ["0555999000","0566666666"],
          new Date()
        )
      }
}