import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';


@Injectable()
export class ServiceContractantAccess {

  constructor(private http: HttpClient){}

  //servicesContractants: ServiceContractant[] = []

  async getAllServicesContractants(): Promise<ServiceContractant[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ServiceContractant[]>(
          `${apiEndpoint}/services-contractants`,
          )
        .subscribe(
          services_contractants => { resolve(services_contractants) },
          error => reject(error)
        )

      });
  }



  async getServiceContractantByIdentifiant(id: String): Promise<ServiceContractant>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ServiceContractant[]>(
          `${apiEndpoint}/services-contractants/identifiants/${id}`,
        )
        .subscribe(
          service_contractant => {
            if(service_contractant.length > 0)
              resolve(service_contractant[0])
            else 
              resolve(null)
          },
          error => reject(error)
        )

      });

  }

  async getServiceContractantByEmail(email: String): Promise<ServiceContractant>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ServiceContractant[]>(
          `${apiEndpoint}/services-contractants/emails/${email}`,
        )
        .subscribe(
          service_contractant => {
            if(service_contractant.length > 0)
              resolve(service_contractant[0])
            else 
              resolve(null)
          },
          error => reject(error)
        )

      });

  }


  async postServiceContractant(serviceContractant: ServiceContractant): Promise<ServiceContractant> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<ServiceContractant>(
          `${apiEndpoint}/services-contractants`, 
          serviceContractant, 
          httpOptions
          )
        .subscribe(
            service_contractant => { resolve(service_contractant) },
            error => reject(error)
        )
      }
    );
  }


  async putServiceContractant(serviceContractant: ServiceContractant, id: String): Promise<ServiceContractant> {
    console.log("The id:", id)
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<ServiceContractant>(
          `${apiEndpoint}/services-contractants/${id}`, 
          serviceContractant, 
          httpOptions
          )
        .subscribe(
            service_contractant => { resolve(service_contractant) },
            error => reject(error)
        )
      }
    );
  }
  

  async deleteServiceContractant(id: String): Promise<ServiceContractant> {
    console.log("The id:", id)

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<ServiceContractant>(
          `${apiEndpoint}/services-contractants/${id}`
          )
        .subscribe(
            service_contractant => { resolve(service_contractant) },
            error => reject(error)
        )
      }
    );
  }
  
} 