import { Component, ViewChild } from '@angular/core';
import { ComplementOffre } from 'src/app/models/complement.offre.model';
import { MatTable, MatDialog } from '@angular/material';
import { CreateDemandeComplementComponent } from 'src/app/dialogs/create-demande-complement/create-demande-complement.component';
import { CreateResultatCopeoOuverturePlisComponent } from 'src/app/dialogs/create-resultat-copeo-ouverture-plis/create-resultat-copeo-ouverture-plis.component';


@Component({
  selector: 'app-ouverture-plis',
  templateUrl: './ouverture_plis.component.html',
  styleUrls: ['./ouverture_plis.component.css']
})

export class OuverturePlisComponent {


  @ViewChild("tableComplementsOffres", {static: true}) tableComplementsOffres: MatTable<ComplementOffre>;



  dataSourceComplementsOffres: ComplementOffre [] = []
  displayedColumnsComplementsOffres: string[] = [
    "soumissionnaire",
    "demande",
    "dateNotification",
    "moyenNotification",
    "reponse"
    ];


  constructor(public dialog: MatDialog) { }


  ngOnInit() {

  }


  


  openDialogCreateResultatPassageOuverturePlisCOPEO(){
    const dialogRef = this.dialog.open(CreateResultatCopeoOuverturePlisComponent, {
      width: '800px',
      data: "resultat commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
  }



  openDialogCreateReponseComplementOffre(){

  }


  
  openDialogCreateDemandeComplement(){
    const dialogRef = this.dialog.open(CreateDemandeComplementComponent, {
      width: '800px',
      data: "resultat commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });    
    this.dataSourceComplementsOffres.push(ComplementOffre.getDummy())
    //console.log(this.recours);
    this.tableComplementsOffres.renderRows();    
  }
  

}
