import { Component, ViewChild } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { OrganePresse } from 'src/app/models/organe.presse.model';
import { Siteweb } from 'src/app/models/siteweb.model';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { CreateOrganePresseComponent } from 'src/app/dialogs/create-organe-presse/create-organe-presse.component';
import { CreateOperateurEconomiqueComponent } from 'src/app/dialogs/create-operateur-economique/create-operateur-economique.component';
import { CreateSitewebComponent } from 'src/app/dialogs/create-siteweb/create-siteweb.component';
import { CreateProlongationComponent } from 'src/app/dialogs/create-prolongation/create-prolongation.component';
import { Prolongation } from 'src/app/models/prolongation.model';
import { formatDate } from '@angular/common';
import { FormControl } from '@angular/forms';


@Component({
  selector: 'app-lancement',
  templateUrl: './lancement.component.html',
  styleUrls: ['./lancement.component.css']
})

export class LancementComponent {

  //@ViewChild(MatTable, null) tableLitiges: MatTable<any>;  //fix this bug What does null and any?

  @ViewChild("tableOrganesPresse", {static: true}) tableOrganesPresse: MatTable<OrganePresse>;
  @ViewChild("tableSitewebsAO", {static: true}) tableSitewebsAO: MatTable<Siteweb>;
  @ViewChild("tableSitewebsC", {static: true}) tableSitewebsC: MatTable<Siteweb>;
  @ViewChild("tableOperateursEconomiques", {static: true}) tableOperateursEconomiques: MatTable<OperateurEconomique>;
  @ViewChild("tableProlongations", {static: true}) tableProlongations: MatTable<Siteweb>;


  currentDay:Date = new Date()
  today = new FormControl(this.currentDay); //test purpose (duree preparation des offres)
  

  appelOffreOuConsultation = "ao"

  dureePreparationDesOffres:number = 15;
  dureeTotalePreparationDesOffres:number = this.dureePreparationDesOffres;
  dureeValidite3Mois:number = 90
  dureeValiditeOffres = this.dureeTotalePreparationDesOffres + this.dureeValidite3Mois
  deadlineValiditeOffres:Date = new Date();

 


  dataSourceOrganesPresse: OrganePresse [] = []
  displayedColumnsOrganesPresse: string[] = [
    'langue',
    'denomination',
    'dateParution',
    'justificatif'
    ];

  dataSourceSitewebsC: Siteweb [] = []
  displayedColumnsSitewebsC: string[] = [
    'denomination',
    'dateParution',
    'lien'
    ];

  dataSourceSitewebsAO: Siteweb [] = []
  displayedColumnsSitewebsAO: string[] = [
    'denomination',
    'dateParution',
    'lien'
    ];

  dataSourceOperateursEconomiques: OperateurEconomique [] = []
  displayedColumnsOperateursEconomiques: string[] = [
    'denomination',
    'moyenNotification',
    'dateParution',
    'justificatif'
    ];


  dataSourceProlongations: Prolongation [] = []
  displayedColumnsProlongations: string[] = [
    'id',
    'jours'
  ];


  passageCommissionChecked:boolean = false;
  additifsChecked:boolean = false;



  constructor(public dialog: MatDialog) { }


  ngOnInit() {
    this.passageCommissionChecked = false
    this.additifsChecked = false

    this.deadlineValiditeOffres = new Date();
    this.deadlineValiditeOffres.setDate(this.currentDay.getDate() + this.dureeValiditeOffres)

  }

  formatDate(date: Date){
    return formatDate(date, "dd/mm/yy", "en")
  }



  openDialogCreateOrganePresse(): void {
    const dialogRef = this.dialog.open(CreateOrganePresseComponent, {
      width: '800px',
      data: "nouveauOrganePresse"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
    console.log()
    this.dataSourceOrganesPresse.push(OrganePresse.getDummy())
    this.tableOrganesPresse.renderRows();

  }


  openDialogCreateOperateurEconomique(): void {
    const dialogRef = this.dialog.open(CreateOperateurEconomiqueComponent, {
      width: '800px',
      data: "nouveauOp.Ecno."
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
    console.log()
    this.dataSourceOperateursEconomiques.push(OperateurEconomique.getDummy())
    this.tableOperateursEconomiques.renderRows();

  }
  
  openDialogCreateSitewebAppelOffres(): void {
    const dialogRef = this.dialog.open(CreateSitewebComponent, {
      width: '800px',
      data: "nouveauOrganePresse"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
    console.log()
    this.dataSourceSitewebsAO.push(Siteweb.getDummy())
    this.tableSitewebsAO.renderRows();

  }

  openDialogCreateSitewebConsultation(): void {
    const dialogRef = this.dialog.open(CreateSitewebComponent, {
      width: '800px',
      data: "nouveauOrganePresse"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
    console.log()
    this.dataSourceSitewebsC.push(Siteweb.getDummy())
    this.tableSitewebsC.renderRows();

  }

  openDialogCreateProlongation(): void {
    const dialogRef = this.dialog.open(CreateProlongationComponent, {
      width: '800px',
      data: "nouveauOrganePresse"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
    console.log()
    this.dataSourceProlongations.push(Prolongation.getDummy())
    this.dureeTotalePreparationDesOffres = this.dureePreparationDesOffres + this.getTotalProlongations()
    this.dureeValiditeOffres = this.dureeTotalePreparationDesOffres + this.dureeValidite3Mois
    this.deadlineValiditeOffres = new Date();
    this.deadlineValiditeOffres.setDate(this.currentDay.getDate() + this.dureeValiditeOffres)
    console.log("new deadline: ", this.deadlineValiditeOffres)
    this.tableProlongations.renderRows();

  }
  getTotalProlongations() {
    return this.dataSourceProlongations.map(t => t.jours).reduce((acc, value) => acc + value, 0);
  }

  
  saveDureePreparationDesOffres(){

  }
}
