export class OrganePresse {
    langue:string;
    denomination: string;
    dateParution: Date;
    justificatif: string;

    constructor (
        langue:string,
        denomination: string,
        dateParution: Date,
        justificatif: string 
    ){
        this.langue = langue
        this.denomination=denomination
        this.dateParution = dateParution
        this.justificatif = justificatif
    }

    static getDummy():OrganePresse {
        return new OrganePresse(
            "fr",
            "NomJournal",
            new Date(),
            "justificatifPath"
        )
    }
}