import { Component, ViewChild } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { Soumissionnaire } from 'src/app/models/soumissionnaire.model';
import { CreateSoumissionnaireComponent } from 'src/app/dialogs/create-soumissionnaire/create-soumissionnaire.component';
import { Prolongation } from 'src/app/models/prolongation.model';
import { CreateProlongationComponent } from 'src/app/dialogs/create-prolongation/create-prolongation.component';
import { FormControl } from '@angular/forms';


@Component({
  selector: 'app-depot-offres',
  templateUrl: './depot_offres.component.html',
  styleUrls: ['./depot_offres.component.css']
})

export class DepotOffresComponent {


  //@ViewChild(MatTable, null) tableLitiges: MatTable<any>;  //fix this bug What does null and any?
  @ViewChild("tableSoumissionnaires", {static: true}) tableSoumissionnaires: MatTable<Soumissionnaire>;
  @ViewChild("tableProlongations", {static: true}) tableProlongations: MatTable<Prolongation>;

  dataSourceProlongations: Prolongation [] = []
  displayedColumnsProlongations: string[] = [
    'id',
    'jours'
  ]

  

  dataSourceSoumissionnaires: Soumissionnaire [] = []
  displayedColumnsSoumissionnaires: string[] = [
    'position',
    'denominationEntreprise',
    'dateDepot',
    'heureDepot'
    ];


  currentDay:Date = new Date()
  today = new FormControl(this.currentDay); //test purpose (duree preparation des offres)
  dureePreparationDesOffres:number = 15;
  dureeTotalePreparationDesOffres:number = this.dureePreparationDesOffres;
  dureeValidite3Mois:number = 90
  dureeValiditeOffres = this.dureeTotalePreparationDesOffres + this.dureeValidite3Mois
  deadlineValiditeOffres:Date = new Date();

  
  constructor(public dialog: MatDialog) { }


  ngOnInit() {
    this.deadlineValiditeOffres = new Date();
    this.deadlineValiditeOffres.setDate(this.currentDay.getDate() + this.dureeValiditeOffres)
  }

  openDialogCreateProlongation(): void {
    const dialogRef = this.dialog.open(CreateProlongationComponent, {
      width: '800px',
      data: "nouveauOrganePresse"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
    console.log()
    this.dataSourceProlongations.push(Prolongation.getDummy())
    this.dureeTotalePreparationDesOffres = this.dureePreparationDesOffres + this.getTotalProlongations()
    this.dureeValiditeOffres = this.dureeTotalePreparationDesOffres + this.dureeValidite3Mois
    this.deadlineValiditeOffres = new Date();
    this.deadlineValiditeOffres.setDate(this.currentDay.getDate() + this.dureeValiditeOffres)
    console.log("new deadline: ", this.deadlineValiditeOffres)
    this.tableProlongations.renderRows();

  }


  getTotalProlongations() {
    return this.dataSourceProlongations.map(t => t.jours).reduce((acc, value) => acc + value, 0);
  }


  openDialogCreateSoumissionnaire(){
    const dialogRef = this.dialog.open(CreateSoumissionnaireComponent, {
      width: '800px',
      data: "nouveau Soumissionnaire"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.dataSourceSoumissionnaires.push(Soumissionnaire.getDummy())
    //console.log(this.recours);
    this.tableSoumissionnaires.renderRows();    
  }


}
